import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import {
  Box,
  Button,
  Card,
  FormHelperText,
  TextField,
  Typography,
} from "@material-ui/core";
import Checkbox from "@material-ui/core/Checkbox";

import { makeStyles } from "@material-ui/core/styles";
import {
  Divider,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
} from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Field, Formik } from "formik";
import Label from "../../components/Label";

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

const StudentToExamsList = (props) => {
  const classes = useStyles();

  const { student, ...other } = props;
  const [error, setError] = useState(null);
  const studeneData=Object.values(student)

function ccyFormat(num){
  // const num=29.98989
  return num.toFixed(2)
}

const getStatusLabel = (invoiceStatus) => {
  console.log("sta", invoiceStatus);

  const map = {
    Fail: {
      color: "error",
      text: "Fail",
    },
    Pass: {
      color: "success",
      text: "Pass",
    }
  };

  const { text, color } = map[invoiceStatus];

  return <Label color={color}>{text}</Label>;
};
  return (
    <Formik
      initialValues={{
        omarks: "",
        submit: null,
      }}
      validationSchema={Yup.object().shape({})}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          alert(JSON.stringify(values));
          // alert("Save Here");
          //   if (onSave) {
          //     onSave();
          //   }
        } catch (err) {
          console.error(err);
          setError(err.message);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        setFieldTouched,
        touched,
        values,
      }) => (
        <form onSubmit={handleSubmit} {...other}>
          {studeneData.length > 0 && (
            <Card sx={{ p: 3, mt: 2 }}>
              {/* <AttendenceTable student={student} /> */}

              <Divider />
              <TableContainer>
                <Table className={classes.table} aria-label="simple table">
                  <TableHead>
                    <TableRow>
                      <TableCell>Roll No</TableCell>
                      <TableCell colSpan={1}>Student Name</TableCell>
                      <TableCell>Marks Obtain </TableCell>
                      <TableCell>Percentage </TableCell>
                      <TableCell>Result</TableCell>
                      <TableCell>Note</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {
                      studeneData.length > 0
                        ? studeneData.map((row, index) => (
                          
                            <TableRow key={row.id}>
                              <TableCell component="th">{row.rollno}</TableCell>
                              <TableCell
                              // id=""
                              //  value={values.id}
                              >
                                {`${row.name}`}
                              </TableCell>

                              <TableCell>{`${row.ObtMarks}/${row.MaxMarkss}`}</TableCell>

                              <TableCell>{ccyFormat((row.ObtMarks * 100) / row.MaxMarkss)}</TableCell>
                              <TableCell>{(row.ObtMarks * 100) / row.MaxMarkss<33? getStatusLabel("Fail"):getStatusLabel("Pass")}</TableCell>

                              <TableCell>
                                <TextField
                                  onChange={handleChange}
                                  value={values.omarks}
                                  // id={row.id}
                                  name={`Omarks`}
                                  type="number"
                                  placeholder="Note"
                                />
                              </TableCell>
                            </TableRow>
                          ))
                        : null
                      // <TableRow
                      //   style={{
                      //     display: "flex",
                      //     justifyContent: "center",
                      //     alignItems: "center",
                      //   }}
                      // >
                      //   Data Not Found{" "}
                      // </TableRow>
                    }
                  </TableBody>
                </Table>
              </TableContainer>

              <Box
                style={{
                  display: "flex",
                  justifyContent: "flex-end",
                  marginTop: 10,
                }}
              >
                <Box sx={{ flexGrow: 1 }} />
                <Button
                  color="primary"
                  disabled={isSubmitting}
                  type="submit"
                  variant="contained"
                >
                  Save
                </Button>
              </Box>
            </Card>
          )}
        </form>
      )}
    </Formik>
  );
};

// StudentToExamsList.propTypes = {
//   onBack: PropTypes.func,
//   onComplete: PropTypes.func,
// };

export default StudentToExamsList;
