import React, { useState, useEffect } from "react";
import {
  Breadcrumbs,
  Button,
  Container,
  Grid,
  Box,
  Typography,
  Link,
} from "@material-ui/core";
import { Helmet } from "react-helmet-async";
import { Link as RouterLink } from "react-router-dom";
import ChevronRightIcon from "../../icons/ChevronRight";
import UploadIcon from "../../icons/Upload";
import DownloadIcon from "../../icons/Download";
import useSettings from "../../hooks/useSettings";
import axios from "axios";
import ExamsRemarkInput1 from "./ExamsmarkInput1";
import toast from "react-hot-toast";
import wait from "../../utils/wait";
import ExamsRemarksInput from "./ExamsRemarksInput";
import {customAxios} from "../apis/Axios";

const ExamsRemark = () => {
  const { settings } = useSettings();
  const [student, setStudent] = useState([]);

  
  const allStudentmarks = async (data) => {
    const studentData = await customAxios.getCalls(
      "exams/api/exams-remarks",
      {
        params: {
          ...data,
        },
      }
    );
    setStudent(studentData);
  };
  

  return (
    <>
      <Helmet>
        <title>Dashboard: Examination </title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 1,
        }}
      >
        <Container maxWidth={settings.compact ? "xl" : false}>
          <Grid container justifyContent="space-between" spacing={3}>
            <Grid item>
              <Typography color="textPrimary" variant="h5">
                Exams Remarks
              </Typography>
              <Breadcrumbs
                aria-label="breadcrumb"
                separator={<ChevronRightIcon fontSize="small" />}
                sx={{ mt: 1 }}
              >
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard"
                  variant="subtitle2"
                >
                  Dashboard
                </Link>
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard/examsGroup"
                  variant="subtitle2"
                >
                  Exam Group List
                </Link>

                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard/examsList"
                  variant="subtitle2"
                >
                  Exam List
                </Link>
                <Typography color="textSecondary">Assign Student</Typography>
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard"
                  variant="subtitle2"
                >
                  Exams Subjects
                </Link>
              </Breadcrumbs>
              <Box
                sx={{
                  mb: -1,
                  mx: -1,
                  mt: 1,
                }}
              >
                <Button
                  color="primary"
                  startIcon={<UploadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  variant="text"
                >
                  Import
                </Button>
                <Button
                  color="primary"
                  startIcon={<DownloadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  variant="text"
                >
                  Export
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Container>
        <ExamsRemarksInput
          student={student}
          allStudentmarks={allStudentmarks}
        />
      </Box>
    </>
  );
};

export default ExamsRemark;
