import React from "react";
import * as Yup from "yup";
import {
  Box,
  Card,
  TextField,
  Typography,
  MenuItem,
  Button,
  FormControl,
  Select,
  InputLabel,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { Formik } from "formik";
import { useState, useEffect } from "react";
import axios from "axios";
import { Section } from "../HelperFunction";
import ExamsmarksList from "./ExamsmarksList";
import { useRef } from "react";
import {customAxios} from "../apis/Axios";
const useStyles = makeStyles({
  table: {
    minWidth: 700,
    // marginTop:10
  },
  hedds: {
    width: "20%",
    // padding: "12px 20px",
    margin: "8px 10px",
    display: "inline-block",
    border: "1px solid #ccc",
    borderRadius: "4px",
    // box-sizing:,
  },
  feild: {
    width: "20%",
    marginLeft: 8,
  },
  feilds: {
    width: "20%",
    marginLeft: 8,
    // marginTop: 8,
    marginBottom: 10,
  },
});

const ExamsmarkInput1 = ({
  student,
  allStudentAttendence,
  setStudent,
  studentsMarks,
  onSave,
  setmarksFeild,
}) => {
  const classes = useStyles();
  const ref = useRef(null);
  const [first, setfirst] = useState([]);

  const [examgroup, setExamgroup] = useState([]);
  const [examsName, setExamsName] = useState([]);
  const [classRoom, setClassRoom] = useState([]);
  const [section, setSection] = useState([]);
  const [subject, setsubject] = useState([]);
  useEffect(async () => {
    const examsGroupData = await customAxios.getCall(
      "exams/examsgroup"
    );
    setExamgroup(examsGroupData);

    const examsNameData = await customAxios.getCall(
      "exams/examsName"
    );
    setExamsName(examsNameData);

    const fun = Section("class/api");
    fun.then((res) => {
      setClassRoom(res);
    });
    const fun1 = Section("class/api/section");
    fun1.then((res) => {
      setSection(res);
    });
  }, []);
  const datas = async () => {
    // console.log(",,,", ref.current.values);
    // return
    const data = await customAxios.getCalls("exams/api/select", {
      params: {
        ...ref.current.values,
      },
    });
    setsubject(data);
  };
  const handleChanges = (e) => {
    // console.log("handle", ref.current.values,first);

    setfirst(e.target.value);

    // setsubject(subject);
  };
  useEffect(async () => {
    if (first == "") {
      return;
    } else {
      datas();
    }
  }, [first]);
  return (
    <>
      <Formik
        initialValues={{
          examsGroup: "",
          examsName: "",
          classid: "",
          sectionid: "",
          Subject: "",
        }}
        validationSchema={Yup.object().shape({})}
        innerRef={ref}
        onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
          alert(JSON.stringify(values), null);
          setmarksFeild(values);
          allStudentAttendence(values);
          // It is important to have it on server to be able to reuse it if user
          // decides to continue later.
          //   if (onNext) {
          //     allStudentAttendence(data);
          //     onNext();
          //   }
          //   setStatus({ success: true });
          //   setSubmitting(false);
          // } catch (err) {
          //   console.error(err);
          //   setStatus({ success: false });
          //   setErrors({ submit: err.message });
          //   setSubmitting(false);
          // }
        }}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          setFieldValue,
          setFieldTouched,
          touched,
          values,
        }) => (
          <form onSubmit={handleSubmit}>
            {/* <Card sx={{ p: 3 }}> */}
            <Box sx={{ mt: 2 }}>
              <FormControl className={classes.feilds}>
                <InputLabel id="examsGroup">Exams Group</InputLabel>

                <Select
                  fullWidth
                  labelId="examsGroup"
                  value={values.examsGroup}
                  onChange={handleChange}
                  id="examsGroup"
                  label="exams Group"
                  name="examsGroup"
                >
                  {examgroup.map((option) => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <FormControl className={classes.feilds}>
                <InputLabel id="examsName">Exams Name</InputLabel>

                <Select
                  fullWidth
                  labelId="examsName"
                  value={values.examsName}
                  onChange={handleChange}
                  id="examsName"
                  label="exams Name"
                  name="examsName"
                >
                  {examsName.map((option) => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <FormControl className={classes.feild}>
                <InputLabel id="classid">Department/Class</InputLabel>

                <Select
                  fullWidth
                  labelId="classid"
                  value={values.classid}
                  onChange={handleChange}
                  id="classid"
                  label="Department/Class"
                  name="classid"
                >
                  {classRoom.map((option) => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.className}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <FormControl className={classes.feild}>
                <InputLabel id="sectionid">Section</InputLabel>

                <Select
                  fullWidth
                  labelId="sectionid"
                  value={first}
                  onChange={(e) => {
                    handleChange(e);
                    handleChanges(e);
                  }}
                  id="sectionid"
                  label="Section"
                  name="sectionid"
                >
                  {section.map((option) => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <FormControl className={classes.feild}>
                <InputLabel id="Subject">Subject</InputLabel>

                <Select
                  fullWidth
                  labelId="Subject"
                  value={values.Subject}
                  onChange={handleChange}
                  id="Subject"
                  label="Subject"
                  name="Subject"
                >
                  {subject.map((option) => (
                    <MenuItem key={option.id} value={option.id}>
                      {option.subjectName}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <Button
                sx={{ mt: 1, mr: 2, ml: 2 }}
                color="primary"
                disabled={isSubmitting}
                type="submit"
                variant="contained"
              >
                Search
              </Button>
            </Box>
            {/* </Card> */}
          </form>
        )}
      </Formik>
      <ExamsmarksList
        studentsMarks={studentsMarks}
        onSave={onSave}
        setStudent={setStudent}
        // hamdleChanges={hamdleChanges}
        // Omarks={Omarks}
        student={student}
      />
    </>
  );
};
export default ExamsmarkInput1;
