import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import {
  Box,
  Button,
  Card,
  FormHelperText,
  TextField,
  Typography,
} from "@material-ui/core";
import Checkbox from "@material-ui/core/Checkbox";

import { makeStyles } from "@material-ui/core/styles";
import {
  Divider,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
} from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Field, Formik } from "formik";

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

const StudentToExamsList = (props) => {
  const { student, Omarks, onSave, studentsMarks,setStudent, ...other } =
    props;
  const classes = useStyles();
  const [omarks, setMarks] = useState({
    ObtMarks:""
  });

  const hamdleChange = (e,index) => {
    let data=[...student]
    setMarks(data[index][e.target.name] = e.target.value);
    setStudent(data)
    //  setMarks({
    //    ...omarks,
    //    [e.target.name]: e.target.value,
    //  });
    console.log(e.target.value);
  };
  const [error, setError] = useState(null);

  useEffect(()=>{
    console.log(omarks)
  },[omarks])
  return (
    <Formik
      initialValues={{
        ObtMarks: omarks,
        submit: null,
      }}
      validationSchema={Yup.object().shape({})}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          // alert(JSON.stringify(values));
          // alert("Save Here");
          if (onSave) {
            onSave();
          }
        } catch (err) {
          console.error(err);
          setError(err.message);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        setFieldTouched,
        touched,
        values,
      }) => (
        <form onSubmit={handleSubmit} {...other}>
          {student.length > 0 && (
            <Card sx={{ p: 3, mt: 2 }}>
              {/* <AttendenceTable student={student} /> */}

              <Divider />
              <TableContainer>
                <Table className={classes.table} aria-label="simple table">
                  <TableHead>
                    <TableRow>
                      <TableCell>Roll No</TableCell>
                      <TableCell colSpan={1}>Student Name</TableCell>
                      <TableCell colSpan={1}>Gender</TableCell>
                      <TableCell colSpan={1}>Father Name</TableCell>
                      {/* <TableCell colSpan={1}>Absent</TableCell> */}
                      <TableCell>Marks Obtain </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {
                      student.length > 0
                        ? student.map((row, index) => (
                            <TableRow key={row.id}>
                              {/* {console.log(row)} */}
                              <TableCell component="th">{row.rollno}</TableCell>
                              <TableCell
                              // id=""
                              //  value={values.id}
                              >
                                {`${row.name}`}
                              </TableCell>
                              <TableCell>{row.gender}</TableCell>

                              <TableCell>{row.fname}</TableCell>

                              <TableCell>
                                <TextField
                                  onChange={(e) => {
                                    hamdleChange(e, index);
                                    studentsMarks(row, e.target.value);
                                  }}
                                  value={ omarks.ObtMarks}
                                  // id={row.id}
                                  name="ObtMarks"
                                  type="number"
                                  placeholder="Enter Marks"
                                />
                              </TableCell>
                            </TableRow>
                          ))
                        : null
                      // <TableRow
                      //   style={{
                      //     display: "flex",
                      //     justifyContent: "center",
                      //     alignItems: "center",
                      //   }}
                      // >
                      //   Data Not Found{" "}
                      // </TableRow>
                    }
                  </TableBody>
                </Table>
              </TableContainer>

              <Box
                style={{
                  display: "flex",
                  justifyContent: "flex-end",
                  marginTop: 10,
                }}
              >
                <Box sx={{ flexGrow: 1 }} />
                <Button
                  color="primary"
                  disabled={isSubmitting}
                  type="submit"
                  variant="contained"
                >
                  Save
                </Button>
              </Box>
            </Card>
          )}
        </form>
      )}
    </Formik>
  );
};

// StudentToExamsList.propTypes = {
//   onBack: PropTypes.func,
//   onComplete: PropTypes.func,
// };

export default StudentToExamsList;
